/* Nunito regular */
@font-face {
  font-family: 'Nunito';
  src: url('Nunito-Regular.woff') format('woff'),
       url('Nunito-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

/* Nunito light */
@font-face {
  font-family: 'Nunito';
  src: url('Nunito-Light.woff') format('woff'),
       url('Nunito-Light.woff2') format('woff2');
  font-weight: 100;
  font-style: normal;
}

/* Nunito bold */
@font-face {
  font-family: 'Nunito';
  src: url('Nunito-SemiBold.woff') format('woff'),
       url('Nunito-SemiBold.woff2') format('woff2');
  font-weight: 500;
  font-style: normal;
}
